.App {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(203, 241, 240);
}

.inner {
  height: 680px;
  width: 455px;
  padding: 1rem;
  border-radius: 20px;
  background-color: #000000;
  box-shadow: 10px 10px 20px #4b4747, -10px -10px 50px #fff;
}

.result {
  font-size: 2rem;
  width: 405px;
  height: 90px;
  scrollbar-width: none;
  margin: auto;
  margin-top: 10px;
  margin-bottom: 20px;
  border-radius: 20px;
  background: rgb(243, 241, 241);
  /* box-shadow: 10px 10px 10px #bebebe, -2px -5px 5px #fff; */
}

.resbox {
  width: 390px;
  height: min-content;
  overflow-y: auto;
  padding-left: 10px;
  padding-right: 10px;
  text-align: right;
  padding-top: 25px;
}

.resbox::-webkit-scrollbar {
  display: none;
}

.btns {
  height: 530px;
  width: 425px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
  border-radius: 20px;
}
